body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.clock-container{
    padding-top: 50px;
    min-height: 100%;
    width: 100%;
    display: flex;
    justify-content: center; 
	align-items: center;
}

.clock-container .clock-base{
    position: relative;
    border: 30px solid #ff9e9e;
    height: 400px;
    width: 400px;
    background-color: #223480;
    border-radius: 100%;
}

.clock-container .clock-base .clock-center {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 12px;
	height: 12px;
	border: 2px solid #fff;
	background-color: #459cff;
	border-radius: 100%;
	margin-left: -6px;
	margin-top: -9px;
	z-index: 100;
}

.clock-container .clock-base .clock-second-hand {
	position: absolute;
	top: 50%;
	left: 40%;
	outline: 2px solid #d81c7a;
	width: 60%;
    /* animation: test-animate 60s linear 0s infinite; */
    -webkit-transform-origin: 17%;
            transform-origin: 17%;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}

/* @keyframes test-animate{
    from {transform: rotate(270deg)}
    to { transform: rotate(624deg)}
} */

.clock-container .clock-base .clock-minute-hand {
	position: absolute;
	top: 50%;
	left: 45%;
	outline: 2px solid #fff;
	width: 45%;
    -webkit-transform-origin: 12%;
            transform-origin: 12%;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}

.clock-container .clock-base .clock-hour-hand {
	position: absolute;
	top: 50%;
	left: 45%;
	outline: 2px solid #fff;
	width: 35%;
    -webkit-transform-origin: 15%;
            transform-origin: 15%;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}
